import { createRouter, createWebHistory } from 'vue-router'

// ÎùºÏö∞ÌÑ∞ ÏÑ§Í≥Ñ
const routes = [
  {
    path: '/',
    name: 'Home',
    component: () => import('@/views/HomeView'), // ÎèôÏ†Å import
  },
  {
    path: '/project',
    name: 'Project',
    component: () => import('@/views/ProjectView'),
  },
  {
    path: '/develop',
    name: 'Develop',
    component: () => import('@/views/DevelopView'),
  },
]

// ÎùºÏö∞ÌÑ∞ ÏÉùÏÑ±
const router = createRouter({
    history: createWebHistory(),
    routes
});

// ÎùºÏö∞ÌÑ∞ Ï∂îÏ∂ú (main.jsÏóêÏÑú import)
export {router}